import {SceneNode} from '@/scene';
import {Connectable} from '@/behaviors';
import {Vector2D} from '@/utils/vector2D';
import {Zone, ZonePing, ZoneSelectionManager} from '@/zone';
import {SceneFov} from '@/scene/SceneFov';

enum MouseState {
  RELEASED,
  PRESSED,
  DRAGGED
}

export class InputHandler extends SceneNode implements Connectable {
  private static _instance = new InputHandler();

  static get instance(): InputHandler {
    return this._instance;
  }

  private _dragStartLocation: Vector2D;
  private _mouseState: MouseState = MouseState.RELEASED;

  onConnect(): void {
    this.onCanvasEvent('mousedown', (e)=>{
      this._dragStartLocation =  new Vector2D(e.offsetX, e.offsetY);
      this._mouseState = MouseState.PRESSED;
    });
    this.onCanvasEvent('mouseup', (e)=>{
      if(this._mouseState==MouseState.PRESSED) {
        const location = SceneFov.instance.screen2world(new Vector2D(e.offsetX, e.offsetY));
        this.onClick(location);
      }
      this._mouseState = MouseState.RELEASED;
    });
    this.onCanvasEvent('mousemove', (e)=>{
      const screenPos = new Vector2D(e.offsetX, e.offsetY);
      if(this._mouseState==MouseState.RELEASED) {
        ZoneSelectionManager.selection?.aim(SceneFov.instance.screen2world(screenPos));
      } else if(this._mouseState==MouseState.PRESSED) {
        if(screenPos.distance(this._dragStartLocation)>5) {
          this._mouseState = MouseState.DRAGGED;
        }
      }

      if(this._mouseState==MouseState.DRAGGED) {
        const offset = screenPos.sub(this._dragStartLocation);
        this._dragStartLocation = screenPos;
        SceneFov.instance.move(offset);
      }
    });
    this.onCanvasEvent('wheel', (e)=>{
      const center = SceneFov.instance.screen2world(new Vector2D(e.offsetX, e.offsetY));
      if(e.deltaY<0) SceneFov.instance.zoomIn(center);
      if(e.deltaY>0) SceneFov.instance.zoomOut(center);
    });
  }

  private onClick(point: Vector2D): void {
    const zone = this.findZone(point);
    if(zone) {
      this.addNode(new ZonePing(zone));
      ZoneSelectionManager.setTarget(zone);
      return;
    }

    if(ZoneSelectionManager.selection?.isAiming) {
      ZoneSelectionManager.selection.fire();
      return;
    }

    ZoneSelectionManager.setTarget(undefined);
  }

  private findZone(point: Vector2D): Zone | undefined {
    for(const zone of ZoneSelectionManager.zones) {
      const radius = zone.radius;
      const distance = zone.position.distance(point);
      if(distance<=radius) return zone;
    }
  }


}
