import {Zone} from '@/zone/Zone';
import {ZoneLink} from '@/zone/ZoneLink';

export class DirectedLink {

  public constructor(
    public readonly link: ZoneLink,
    public readonly from: Zone,
    public readonly to: Zone) {
  }

  public invert(): DirectedLink {
    return new DirectedLink(this.link, this.to, this.from);
  }

  public isForward(): boolean {
    return this.link.to === this.to;
  }

}
