import {Zone} from '@/zone/Zone';
import {Drawable} from '@/behaviors';
import {SceneNode} from '@/scene';
import {CanvasLayer} from '@/utils/CanvasLayer';
import {Projectile} from '@/zone/Projectile';
import {DirectedLink} from '@/zone/DirectedLink';

export class ZoneLink extends SceneNode implements Drawable{

  public readonly projectilesForward = new Set<Projectile>()
  public readonly projectilesBackward = new Set<Projectile>()

  public constructor(private _from: Zone, private _to: Zone) {
    super();
    _from.addLink(this);
    _to.addLink(this);
  }

  get from(): Zone {
    return this._from;
  }

  get to(): Zone {
    return this._to;
  }

  public directedFrom(zone: Zone): DirectedLink {
    if(this._from==zone)
      return new DirectedLink(this, this._from, this._to);
    else
      return new DirectedLink(this, this._to, this._from);
  }

  public getProjectileSet(forward: boolean): Set<Projectile> {
    return forward?this.projectilesForward:this.projectilesBackward;
  }

  public getZIndex(): number {
    return CanvasLayer.ZoneLinks;
  }

  public Draw(ctx: CanvasRenderingContext2D): void {
    ctx.beginPath();
    ctx.lineWidth = 1.3;
    ctx.setLineDash([8]);
    ctx.lineTo(this._from.position.x, this._from.position.y);
    ctx.lineTo(this._to.position.x, this._to.position.y);
    ctx.strokeStyle = '#434349'+'80';
    ctx.stroke();

    const v = this._to.position.sub(this._from.position).orthogonal().normalize();
    ctx.beginPath();
    ctx.lineWidth = 0.2;
    ctx.setLineDash([]);
    ctx.lineTo(this._from.position.x+v.x*2, this._from.position.y+v.y*2);
    ctx.lineTo(this._to.position.x+v.x*2, this._to.position.y+v.y*2);
    ctx.strokeStyle = '#7d7f8d'+'50';
    ctx.stroke();

    ctx.beginPath();
    ctx.lineWidth = 0.2;
    ctx.setLineDash([]);
    ctx.lineTo(this._from.position.x-v.x*2, this._from.position.y-v.y*2);
    ctx.lineTo(this._to.position.x-v.x*2, this._to.position.y-v.y*2);
    ctx.strokeStyle = '#7d7f8d'+'50';
    ctx.stroke();
  }

}
