import {Canvas} from '@/utils/Canvas';
import {Zone} from '@/zone/Zone';
import {BehaviorManager} from '@/behaviors/BehaviorManager';
import {Vector2D} from '@/utils/vector2D';
import {ZoneLink} from '@/zone/ZoneLink';
import {Scene} from '@/scene';
import {Background} from '@/game/Background';
import {InputHandler} from '@/input/InputHandler';
import {Level1AI} from '@/game/Level1AI';

export class Game {
  private _lastTimestamp = 0
  private _zones: Zone[]
  private _links: ZoneLink[]

  public Start(): void {
    Scene.instance.addNode(new Background());
    Scene.instance.addNode(InputHandler.instance);

    this._zones = [
      new Zone(new Vector2D(-200,-150), 1),
      new Zone(new Vector2D(-100, 150), 0),
      new Zone(new Vector2D(   0,   0), 0),
      new Zone(new Vector2D( 200, 200), 2),
      new Zone(new Vector2D( 300, -50), 0),
    ];

    this._links = [
      new ZoneLink(this._zones[0], this._zones[1]),
      new ZoneLink(this._zones[0], this._zones[2]),
      new ZoneLink(this._zones[1], this._zones[2]),
      new ZoneLink(this._zones[2], this._zones[3]),
      new ZoneLink(this._zones[2], this._zones[4]),
      new ZoneLink(this._zones[3], this._zones[4]),
    ];

    const scene = Scene.instance;

    this._zones.forEach(entry=>scene.addNode(entry));
    this._links.forEach(entry=>scene.addNode(entry));
    scene.addNode(new Level1AI(this._zones));

    window.requestAnimationFrame(() => {
      // set initial timestamp
      this._lastTimestamp = Date.now();

      // start update loop
      this.Update();
    });
  }

  private Update(): void {
    const deltaTime = (Date.now() - this._lastTimestamp) / 1000;
    this._lastTimestamp = Date.now();

    Canvas.fixDpi();

    BehaviorManager.Update(deltaTime);
    BehaviorManager.Draw(Canvas.ctx);

    // Invoke on next frame
    window.requestAnimationFrame(() => this.Update());
  }
}
