import {Vector2D} from '@/utils/vector2D';
import {Drawable, Updatable, Connectable} from '@/behaviors';
import {Scene, SceneNode} from '@/scene';
import {ZoneSelectionManager} from '@/zone/ZoneSelectionManager';
import {ZoneLink} from '@/zone/ZoneLink';
import {CanvasLayer} from '@/utils/CanvasLayer';
import {Projectile} from '@/zone/Projectile';
import {PlayerTemplates} from '@/game/PlayerTemplate';
import {EndScreen} from '@/game/EndScreen';
import {DirectedLink} from '@/zone/DirectedLink';

export class Zone extends SceneNode implements Drawable, Updatable, Connectable {

  private _maxShield: number;
  private _shield: number;
  private _maxValue: number;
  private _value: number;

  private _links = new Set<ZoneLink>();
  private _owner: number;
  private _color: string;

  public constructor(private _position: Vector2D, _owner: number) {
    super();
    this.setOwner(_owner);
    this._maxValue = 150;
    this._value = (_owner == 0) ? 10 : 0;
    [this._maxShield, this._shield] = [10, 0];
  }

  onConnect(): void {
    ZoneSelectionManager.zones.add(this);
    this.nodeDisconnectedEvent.subscribe(() => {
      ZoneSelectionManager.zones.delete(this);
    });
  }

  public get owner(): number {
    return this._owner;
  }

  private setOwner(newOwner: number): void {
    this._owner = newOwner;
    this._color = PlayerTemplates.getTemplate(newOwner).color;
  }

  get value(): number {
    return this._value;
  }

  get shield(): number {
    return this._shield;
  }

  get maxShield(): number {
    return this._maxShield;
  }

  public get position(): Vector2D {
    return this._position;
  }

  public get color(): string {
    return this._color;
  }

  public get links(): ReadonlySet<ZoneLink> {
    return this._links;
  }

  public addLink(link: ZoneLink): void {
    this._links.add(link);
  }

  public Update(deltaTime: number): void {
    if (this._owner != 0) {
      this._value += deltaTime * 2;
      if (this._value > this._maxValue) {
        this._value = this._maxValue;
      }
    }

    this._shield += deltaTime * 3;
    if (this._shield > this._maxShield) {
      this._shield = this._maxShield;
    }
  }

  public get radius(): number {
    return 18 + this._value / 5;
  }

  public getZIndex(): number {
    return CanvasLayer.Zones;
  }

  public fire(link: DirectedLink, power: number): void {
    Scene.instance.addNode(new Projectile(link, power));
    this._value -= power;
  }

  public onHit(projectile: Projectile): void {
    const friendly = projectile.owner == this.owner;
    if (friendly) {
      this._value += projectile.power;
    } else {
      this._shield -= projectile.power;
      if (this._shield < 0) {
        this._value += this._shield;
        this._shield = 0;
        if (this._value < 0) {
          this.setOwner(projectile.owner);
          this._value = -this.value;
          this.checkWinCondition();
        }
      }
    }
  }

  private checkWinCondition(): void {
    const lastPlayerRemaining = this.lastPlayerRemaining();
    if (!lastPlayerRemaining) return;

    for(const node of Scene.instance.childrens) {
      node.parent = undefined;
    }

    Scene.instance.addNode(new EndScreen((lastPlayerRemaining==1)?'WIN':'LOST'));
  }

  private lastPlayerRemaining(): number | undefined {
    let remainingOwner: number | undefined = undefined;
    for(const zone of ZoneSelectionManager.zones) {
      if(zone.owner==0) continue;
      if(zone.owner==remainingOwner) continue;
      if (remainingOwner!=undefined) return undefined;
      remainingOwner = zone.owner;
    }
    return remainingOwner;
  }

  public Draw(ctx: CanvasRenderingContext2D): void {
    const radius = this.radius;

    ctx.beginPath();
    ctx.arc(this._position.x, this._position.y, radius, 0, Math.PI * 2);
    ctx.fillStyle = '#FFFFFF';
    ctx.fill();
    ctx.fillStyle = this._color + '90';
    ctx.fill();

    ctx.beginPath();
    ctx.arc(this._position.x, this._position.y, radius - 2, 0, Math.PI * 2);
    ctx.lineWidth = 5;
    ctx.setLineDash([]);
    ctx.strokeStyle = this._color + '20';
    ctx.stroke();

    ctx.beginPath();
    ctx.arc(this._position.x, this._position.y, radius - 4, 0, Math.PI * 2);
    ctx.lineWidth = 7;
    ctx.setLineDash([]);
    ctx.strokeStyle = this._color + '20';
    ctx.stroke();

    ctx.beginPath();
    ctx.arc(this._position.x, this._position.y, radius, 0, Math.PI * 2);
    ctx.lineWidth = 1;
    ctx.setLineDash([]);
    ctx.strokeStyle = this._color;
    ctx.stroke();

    // const shieldRadius = radius + 4
    // ctx.beginPath()
    // ctx.arc(this._position.x, this._position.y, shieldRadius, 0, Math.PI * 2)
    // ctx.strokeStyle = this._color
    // ctx.lineWidth = 4
    // ctx.setLineDash([Math.PI * shieldRadius / Math.log2(this._value)])
    // ctx.stroke()

    const shieldRadius = radius - 6;
    ctx.beginPath();
    ctx.arc(this._position.x, this._position.y, shieldRadius, 0, Math.PI * 2 * this._shield / this._maxShield);
    ctx.strokeStyle = '#ffffff';
    ctx.lineWidth = 4;
    ctx.lineCap = 'round';
    ctx.setLineDash([0, Math.PI * shieldRadius * 2 / this._maxShield]);
    ctx.stroke();

    ctx.textAlign = 'center';
    ctx.textBaseline = 'middle';
    ctx.fillStyle = '#1a1805';
    ctx.font = '20px serif';
    ctx.fillText(Math.floor(this._value).toString(), this._position.x, this._position.y + 2);
  }
}
