import {SceneNode} from '@/scene';
import {BehaviorManager} from '@/behaviors/BehaviorManager';

export class Scene extends SceneNode {

  private static _instance: Scene = new Scene()

  public static get instance(): Scene {
    return Scene._instance;
  }

  private constructor() {
    super();
    this.descendantAddedEvent.subscribe((descendant) => {
      this.forEachChildren(descendant, (node) => {
        this.addToScene(node);
      });
    });
    this.descendantRemovedEvent.subscribe((descendant) => {
      this.forEachChildren(descendant, (node) => {
        this.delFromScene(node);
      });
    });
  }

  private forEachChildren(node: SceneNode, callback: (node: SceneNode) => void): void {
    callback(node);

    const childrens = node.childrens;
    childrens.forEach((child: SceneNode) => {
      this.forEachChildren(child, callback);
    });
  }

  private addToScene(node: SceneNode): void {
    BehaviorManager.add(node);
  }

  private delFromScene(node: SceneNode): void {
    BehaviorManager.delete(node);
  }

}
