import {SceneNode} from '@/scene';
import {Updatable} from '@/behaviors';
import {DirectedLink, Zone} from '@/zone';

export class Level1AI extends SceneNode implements Updatable {

  private _rules: Updatable[] = [];

  constructor(private _zone: Zone[]) {
    super();
    this._rules.push(this.tryCapture(2));
    this._rules.push(this.tryCapture(4));
    this._rules.push(this.tryCapture(3));
    this._rules.push(this.tryCapture(1));
    this._rules.push(this.tryCapture(0));
    // this._rules.push(this.trySupply(3, 2));
    // this._rules.push(this.trySupply(4, 2));
  }

  Update(deltaTick: number): void {
    for (const rule of this._rules) {
      rule.Update(deltaTick);
    }
  }

  private tryCapture(targetIdx: number): Updatable {
    const target = this._zone[targetIdx];
    const sources = this.getLinked(target).map(link=>link.invert());

    return {
      Update: function (): void {
        if (target.owner == 2) return;

        let expectedPower = 20;
        if (target.owner != 0) expectedPower = target.value + target.maxShield + 15;

        let ownPower = 0;
        for (const src of sources) {
          if (src.from.owner == 2 && src.from.value > 20) ownPower += src.from.value;
        }

        if (ownPower > expectedPower) {
          for (const src of sources) {
            if (src.from.owner == 2) src.from.fire(src, src.from.value);
          }
        }
      }
    };
  }

  // private trySupply(fromIdx: number, targetIdx: number): Updatable {
  //   const target = this._zone[targetIdx];
  //   const from = this._zone[fromIdx];
  //   const linked = this.getLinked(from);
  //
  //   return {
  //     Update: function (): void {
  //       if (target.owner != 2) return;
  //       if (from.owner != 2) return;
  //       for (const l of linked) {
  //         if (l.to.owner != 2) return;
  //       }
  //
  //       if (from.value<1) return;
  //       from.fire(target, from.value);
  //     }
  //   };
  // }

  private getLinked(zone: Zone): DirectedLink[] {
    const result: DirectedLink[] = [];
    for (const link of zone.links) {
      result.push(link.directedFrom(zone));
    }
    return result;
  }
}
