import {SceneNode} from '@/scene';
import {Drawable, Updatable} from '@/behaviors';
import {Vector2D} from '@/utils/vector2D';
import {Canvas} from '@/utils/Canvas';
import {CanvasLayer} from '@/utils/CanvasLayer';

class Star {
  public cycle: number = 2+Math.random()*4
  public offset: number = Math.random()*this.cycle
  public position: Vector2D = new Vector2D(Math.random()*Canvas.size.x,Math.random()*Canvas.size.y)
  public size: number = Math.random()*(Math.random()*5+2)
  public shining: number = Math.floor(128*Math.random()+128)
}

export class EndScreen extends SceneNode implements Drawable, Updatable {

  private _stars: Star[] = []

  constructor(private _message: string) {
    super();
    for (let i=0;i<1000;i++){
      this._stars.push(new Star());
    }
  }

  Update(deltaTick: number): void {
    this._stars.forEach(star=>{
      star.offset+=deltaTick;
      if (star.offset>star.cycle){
        star.offset=0;
      }
    });

  }

  Draw(ctx: CanvasRenderingContext2D): void {
    ctx.fillStyle = '#00002d';
    ctx.shadowColor = '#ffa500';
    ctx.shadowBlur = 30;
    ctx.fillRect(0, 0, Canvas.size.x, Canvas.size.y);

    this._stars.forEach(star=>{
      if(star.offset>1){
        return;
      }
      ctx.beginPath();
      ctx.arc(star.position.x, star.position.y, star.size*Math.sin(star.offset*Math.PI), 0, Math.PI * 2);
      ctx.fillStyle = '#ffdb9c'+star.shining.toString(16);
      ctx.fill();
    });

    ctx.font = '50px Lucida Handwriting';
    ctx.fillText(this._message, 400, 300);

    ctx.shadowBlur = 0;
  }

  getZIndex(): number {
    return CanvasLayer.Background;
  }

}
