import {DeferredEvent, GameEvent, Subscription} from '@/utils/events';
import {Canvas} from '@/utils/Canvas';

export type SceneNodeChildrenCallback = (node: SceneNode) => void

export class SceneNode {
  private _parent?: SceneNode;
  private _childrens: Set<SceneNode> = new Set<SceneNode>();
  private _childrenAdded = new DeferredEvent<SceneNodeChildrenCallback>();
  private _childrenRemoved = new DeferredEvent<SceneNodeChildrenCallback>();
  private _descendantAdded = new DeferredEvent<SceneNodeChildrenCallback>();
  private _descendantRemoved = new DeferredEvent<SceneNodeChildrenCallback>();
  private _nodeDisconnected = new DeferredEvent<SceneNodeChildrenCallback>();

  public get childrens(): Set<SceneNode> {
    return this._childrens;
  }

  public get childrenAddedEvent(): GameEvent<SceneNodeChildrenCallback> {
    return this._childrenAdded;
  }

  public get childrenRemovedEvent(): GameEvent<SceneNodeChildrenCallback> {
    return this._childrenRemoved;
  }

  public get descendantAddedEvent(): GameEvent<SceneNodeChildrenCallback> {
    return this._descendantAdded;
  }

  public get descendantRemovedEvent(): GameEvent<SceneNodeChildrenCallback> {
    return this._descendantRemoved;
  }

  public get nodeDisconnectedEvent(): GameEvent<SceneNodeChildrenCallback> {
    return this._nodeDisconnected;
  }

  public get parent(): SceneNode | undefined {
    return this._parent;
  }

  public set parent(parent: SceneNode | undefined) {
    if (this._parent===parent) return;

    if (this._parent!=undefined) {
      this._parent.removeInternally(this);
      this._parent = undefined;
      this._nodeDisconnected.fire(this);
    }

    if (parent!=undefined) {
      this._parent = parent;
      this._parent.addInternally(this);
    }
  }

  public addNode(node: SceneNode): void {
    if(node._parent!==this)
      node.parent = this;
  }

  public removeNode(node: SceneNode): void {
    if(node._parent===this)
      node.parent = undefined;
  }

  public onCanvasEvent<K extends keyof HTMLElementEventMap>(
    type: K,
    listener: (this: HTMLCanvasElement, ev: HTMLElementEventMap[K]) => unknown,
    options?: boolean | AddEventListenerOptions): Subscription{
    const canvasSubscription = Canvas.onCanvasEvent(type, listener, options);
    const disconnectSubscription = this._nodeDisconnected.subscribe(()=>{
      canvasSubscription.stop();
    });

    return {
      stop: function (): void {
        disconnectSubscription.stop();
        canvasSubscription.stop();
      }
    };
  }

  private addInternally(node: SceneNode): void {
    if (this._childrens.add(node)) {
      this._childrenAdded.fire(node);
      this._descendantAdded.fire(node);
      let owner = this._parent;
      while (owner!=undefined) {
        owner._descendantAdded.fire(node);
        owner = owner._parent;
      }
    }
  }

  private removeInternally(node: SceneNode): void {
    if (this._childrens.delete(node)) {
      this._childrenRemoved.fire(node);
      this._descendantRemoved.fire(node);
      let owner = this._parent;
      while (owner!=undefined) {
        owner._descendantRemoved.fire(node);
        owner = owner._parent;
      }
    }
  }

}
