import {Vector2D} from '@/utils/vector2D';
import {Subscription} from '@/utils/events';

export class Canvas {
  private constructor() { /* make it unaccessible */
  }

  private static _elm: HTMLCanvasElement
  private static _ctx: CanvasRenderingContext2D
  private static _size: Vector2D = new Vector2D(1920, 1080)

  public static get size(): Vector2D {
    return Canvas._size;
  }

  static get ctx(): CanvasRenderingContext2D {
    return Canvas._ctx;
  }

  public static fixDpi(): void {
    const computedStyle = getComputedStyle(Canvas._elm);
    Canvas._size.x = parseInt(computedStyle.width);
    Canvas._size.y = parseInt(computedStyle.height);
    Canvas._elm.setAttribute('width', computedStyle.width);
    Canvas._elm.setAttribute('height', computedStyle.height);
  }

  public static onCanvasEvent<K extends keyof HTMLElementEventMap>(
    type: K,
    listener: (this: HTMLCanvasElement, ev: HTMLElementEventMap[K]) => void,
    options?: boolean | AddEventListenerOptions): Subscription {
    Canvas._elm.addEventListener(type, listener, options);
    return {
      stop: function (): void {
        Canvas._elm.removeEventListener(type, listener, options);
      }
    } as Subscription;
  }

  private static _initialize = ((): void => {
    const canvas = document.createElement('canvas');
    document.body.appendChild(canvas);

    Canvas._elm = canvas;

    const ctx = Canvas._elm.getContext('2d');
    if (!ctx) {
      throw new Error('Context identifier is not supported');
    }

    Canvas._ctx = ctx;
    Canvas.fixDpi();
  })()
}
