import {Drawable, Updatable, Connectable} from '@/behaviors';
import {SceneNode} from '@/scene';

export class BehaviorManager {

  private static _drawable: Set<Drawable>[] = []
  private static _updatable: Set<Updatable> = new Set<Updatable>()

  public static add(entity: SceneNode): void {
    this.forEachLayer(entity, (layer, value)=>layer.add(value));
    if('onConnect' in entity) {
      (entity as Connectable).onConnect();
    }
  }

  public static delete(entity: SceneNode): void {
    this.forEachLayer(entity, (layer, value)=>layer.delete(value));
  }

  public static Draw(ctx: CanvasRenderingContext2D): void {
    BehaviorManager._drawable.forEach(layer => {
      layer.forEach(entry => entry.Draw(ctx));
    });
  }

  public static Update(deltaTick: number): void {
    BehaviorManager._updatable.forEach(entry => entry.Update(deltaTick));
  }

  private static forEachLayer(entity: SceneNode, callback: <T>(layer: Set<T>, entity: T) => void): void {
    if ('Draw' in entity) {
      const zIndex = (entity as Drawable).getZIndex();
      callback(BehaviorManager.getZLayer(zIndex), entity);
    }
    if ('Update' in entity) {
      callback(BehaviorManager._updatable, entity);
    }
  }

  private static getZLayer(zIndex: number): Set<Drawable> {
    let layer = BehaviorManager._drawable[zIndex];
    if (layer === undefined) {
      layer = new Set<Drawable>();
      BehaviorManager._drawable[zIndex] = layer;
    }
    return layer;
  }
}
