import {SubscriptionList, GameEvent, Subscription} from '@/utils/events';


export class DeferredEvent<HANDLER extends Function> implements GameEvent<HANDLER>{
  private _subscriptions = new SubscriptionList<HANDLER>();

  public subscribe(handler: HANDLER): Subscription {
    return this._subscriptions.addEntry(handler);
  }

  public fire(...args: unknown[]): void {
    this._subscriptions.forEach(handler=>{
      handler(...args);
    });
  }


}
