import {Canvas} from '@/utils/Canvas';
import {Vector2D} from '@/utils/vector2D';

export class SceneFov {

  private static _instance: SceneFov = new SceneFov()

  public static get instance(): SceneFov {
    return SceneFov._instance;
  }

  private _translation: Vector2D;
  private _scale = 1;

  private constructor() {
    this._translation = new Vector2D(Canvas.size.x/2, Canvas.size.y/2);
  }

  public move(offset: Vector2D): void {
    this._translation = this._translation.add(offset);
  }

  public zoomIn(center: Vector2D): void {
    this.setScale(center, this._scale * 1.1);
  }

  public zoomOut(center: Vector2D): void {
    this.setScale(center, this._scale / 1.1);
  }

  private setScale(center: Vector2D, newScale: number): void {
    this._translation = this._translation.add(center.mul(this._scale-newScale));
    this._scale = newScale;
  }

  public setNativeFov(): void {
    Canvas.ctx.setTransform(1, 0, 0, 1, 0, 0);
  }

  public setLocalFov(): void {
    Canvas.ctx.setTransform(
      this._scale, 0,
      0, this._scale,
      this._translation.x, this._translation.y
    );
  }

  public screen2world(location: Vector2D): Vector2D {
    return new Vector2D(
      (location.x - this._translation.x) / this._scale,
      (location.y - this._translation.y) / this._scale
    );
  }
}
