import {Zone} from '@/zone/Zone';
import {Scene, SceneNode} from '@/scene';
import {Drawable} from '@/behaviors';
import {Vector2D} from '@/utils/vector2D';
import {ZoneLink} from '@/zone/ZoneLink';
import {CanvasLayer} from '@/utils/CanvasLayer';
import {DirectedLink} from '@/zone/DirectedLink';

class AimProjection {
  constructor(public projectedTarget: Vector2D,
              public projectedDistance: number,
              public projectedLength: number,
              public link: DirectedLink) {
  }

}

export class ZoneSelection extends SceneNode implements Drawable {
  private _aimTarget?: AimProjection;

  constructor(private _target: Zone) {
    super();
  }

  public getZIndex(): number {
    return CanvasLayer.ZoneSelection;
  }

  public get isAiming(): boolean {
    return this._aimTarget !== undefined;
  }

  public fire(): void {
    if (this._aimTarget === undefined) {
      return;
    }
    const power = Math.floor(this._aimTarget.projectedLength * this._target.value);
    if (power > 0) this._target.fire(this._aimTarget.link, power);
  }

  Draw(ctx: CanvasRenderingContext2D): void {
    ctx.setLineDash([]);
    ctx.beginPath();
    ctx.arc(this._target.position.x, this._target.position.y, this._target.radius + 10, 0, Math.PI * 2);
    ctx.lineWidth = 5;
    ctx.strokeStyle = this._target.color + '40';
    ctx.stroke();
    ctx.beginPath();
    ctx.arc(this._target.position.x, this._target.position.y, this._target.radius + 12, 0, Math.PI * 2);
    ctx.lineWidth = 1;
    ctx.strokeStyle = this._target.color + '80';
    ctx.stroke();

    if (this._aimTarget) {
      ctx.beginPath();
      ctx.lineTo(this._target.position.x, this._target.position.y);
      ctx.lineTo(this._aimTarget.projectedTarget.x, this._aimTarget.projectedTarget.y);
      ctx.lineWidth = 6;
      ctx.setLineDash([]);
      ctx.lineCap = 'round';
      ctx.strokeStyle = this._target.color + '90';
      ctx.stroke();

      ctx.textAlign = 'right';
      ctx.textBaseline = 'middle';
      ctx.fillStyle = '#fff6c0';
      ctx.font = '14px serif';
      ctx.fillText(Math.floor(this._aimTarget.projectedLength * this._target.value).toString(), this._aimTarget.projectedTarget.x + 10, this._aimTarget.projectedTarget.y - 10);
    }
  }

  public aim(point: Vector2D): void {
    this._aimTarget = undefined;
    if (this._target.owner != 1) return;

    this._target.links.forEach(link => {
      const projection = this.project(point, link);
      if (!projection) return;

      if (!this._aimTarget || projection.projectedDistance < this._aimTarget.projectedDistance)
        this._aimTarget = projection;
    });

    if (this._aimTarget !== undefined){
      const _aimTarget: AimProjection = this._aimTarget;

      if(_aimTarget.projectedDistance > 50) {
        this._aimTarget = undefined;
      }
    }
  }

  private project(point: Vector2D, link: ZoneLink): AimProjection | undefined {
    const directedLink = link.directedFrom(this._target);
    const pStart: Vector2D = this._target.position;
    const pEnd: Vector2D = directedLink.to.position;
    const vLink = pEnd.sub(pStart);
    const vOrtho = vLink.orthogonal().normalize();
    const vD = point.sub(pStart);

    const W = vLink.x * vOrtho.y - vLink.y * vOrtho.x;
    const Wa = vD.x * vOrtho.y - vD.y * vOrtho.x;
    const Wb = vLink.x * vD.y - vLink.y * vD.x;
    const a = Wa / W;
    const b = Wb / W;

    if (a < 0 || a > 1) return undefined;

    const distance = vLink.length();
    const deadZoneSize = 20;
    const fromDeadZone = (this._target.radius+deadZoneSize)/distance;
    const toDeadZone = (directedLink.to.radius+deadZoneSize)/distance;
    let limitedPower = a;
    if(limitedPower<fromDeadZone) limitedPower = fromDeadZone;
    if(limitedPower>1-toDeadZone) limitedPower = 1-toDeadZone;

    const scaledPower = (limitedPower-fromDeadZone) / (1-fromDeadZone-toDeadZone);

    return new AimProjection(
      pStart.add(vLink.mul(limitedPower)),
      Math.abs(b), scaledPower,
      directedLink
    );
  }
}

export class ZoneSelectionManager {
  private static _selection?: ZoneSelection;
  private static _zones = new Set<Zone>();

  static get zones(): Set<Zone> {
    return this._zones;
  }

  static get selection(): ZoneSelection | undefined {
    return this._selection;
  }

  public static setTarget(target?: Zone): void {
    if (ZoneSelectionManager._selection !== undefined) {
      ZoneSelectionManager._selection.parent = undefined;
      ZoneSelectionManager._selection = undefined;
    }

    if (target !== undefined) {
      ZoneSelectionManager._selection = new ZoneSelection(target);
      Scene.instance.addNode(ZoneSelectionManager._selection);
    }
  }
}
