import {Subscription} from '@/utils/events/Subscription';

export class SubscriptionList<T> {
  private entries: Set<T> = new Set<T>()

  public addEntry(entry: T): Subscription {
    const entries = this.entries;
    entries.add(entry);
    return ({
      stop: function (): void {
        entries.delete(entry);
      }
    } as Subscription);
  }

  public forEach(callback: (value: T) => void): void {
    this.entries.forEach(callback);
  }
}
