import {Drawable, Updatable} from '@/behaviors';
import {SceneNode} from '@/scene';
import {CanvasLayer} from '@/utils/CanvasLayer';
import {Vector2D} from '@/utils/vector2D';
import {DirectedLink} from '@/zone/DirectedLink';

export class Projectile extends SceneNode implements Drawable, Updatable{

  private _speed = 50;
  private _offset = 0;
  private _position: Vector2D;
  private _direction: Vector2D;
  private readonly _totalDistance: number;
  private readonly _owner: number;
  private readonly _color: string;

  public constructor(
    public readonly link: DirectedLink,
    private _power: number) {
    super();
    this._owner = link.from.owner;
    this._color = link.from.color;
    this._position = link.from.position;
    this._direction = link.to.position.sub(link.from.position).normalize();
    this._offset = link.from.radius;
    this._totalDistance = link.to.position.sub(link.from.position).length();

    this.getProjectileSet().add(this);
  }

  get power(): number {
    return this._power;
  }

  get owner(): number {
    return this._owner;
  }

  Update(deltaTick: number): void {
    this._offset += deltaTick*this._speed;
    for(const other of this.getOpositeProjectileSet()) {
      if(this._offset < this._totalDistance - other._offset) continue;
      if(this._owner == other._owner) continue;
      const powerDiff = this._power - other._power;
      if (powerDiff>0) {
        this._power = powerDiff;
        other.unlink();
      } else {
        other._power = -powerDiff;
        this.unlink();
        return;
      }
    }
    if(this._offset>this._totalDistance-this.link.to.radius) {
      this.link.to.onHit(this);
      this.unlink();
    } else {
      this._position = this.link.from.position.add(this._direction.mul(this._offset));
    }
  }

  public getProjectileSet(): Set<Projectile> {
    return this.link.link.getProjectileSet(this.link.isForward());
  }

  public getOpositeProjectileSet(): Set<Projectile> {
    return this.link.link.getProjectileSet(!this.link.isForward());
  }

  private unlink(): void {
    this.getProjectileSet().delete(this);
    this.parent = undefined;
  }

  public getZIndex(): number {
    return CanvasLayer.Projectiles;
  }

  public Draw(ctx: CanvasRenderingContext2D): void {
    ctx.fillStyle = this._color;
    ctx.shadowColor = '#ffffff50';
     ctx.shadowBlur = 5;

    ctx.beginPath();
    ctx.arc(this._position.x, this._position.y, Math.log2(this._power+2), 0, Math.PI * 2);
    ctx.fill();

    ctx.shadowBlur = 0;
  }

}
