import {Zone} from '@/zone/Zone';
import {Drawable, Updatable} from '@/behaviors';
import {SceneNode} from '@/scene';
import {CanvasLayer} from '@/utils/CanvasLayer';

export class ZonePing extends SceneNode implements Drawable, Updatable{
  private static _INTERVAL = 0.5;
  private static _SCALE = ZonePing._INTERVAL*ZonePing._INTERVAL/(4*20);

  private _phase = 0
  private _size = 0

  public constructor(private _target: Zone) {
    super();
  }

  public getZIndex(): number {
    return CanvasLayer.ZoneSelection;
  }

  Update(deltaTick: number): void {
    this._phase += deltaTick;
    this._size = this._target.radius - this._phase*(this._phase-ZonePing._INTERVAL)/ZonePing._SCALE;
    // if (this._phase>ZonePing._INTERVAL) {
    //   this.parent = undefined
    // }
    if(this._size<0) {
      this.parent = undefined;
    }
  }

  public Draw(ctx: CanvasRenderingContext2D): void {
    ctx.beginPath();
    ctx.arc(this._target.position.x, this._target.position.y, this._size, 0, Math.PI * 2);
    ctx.fillStyle = this._target.color + '80';
    ctx.fill();
  }

}
